export default{
	data(){
		return{
			id:null,
			details:{}
		}
	},
	onLoad(e){
		this.id=e.id
	},
	onShow(){
		this.getMsgDetails()
	},
	methods:{
		getMsgDetails(){
			this.$http.post("sMessage/getComplaintDetail",{
				id:this.id
			}).then(res=>{
				if(res.code==100){
					this.details=res.info
				}
			})
		}
	}
}
